$heading: #121212;

.pxr-theme-body-class {
   font-size: 16px;
   line-height: 150%;

   h1,
   h2,
   h3,
   h4,
   h5,
   h6 {
      color: $heading;
      line-height: 105%;
   }

   h1 {
      font-size: 50px;
   }

   h2 {
      font-size: 38px;
   }

   h3 {
      font-size: 29px;
   }

   h4 {
      font-size: 22px;
   }

   h5 {
      font-size: 18px;
   }

   h6 {
      font-size: 16px;
   }
}
